      <view class="info-item">
        <text class="info-label">ÂÖ¨ÂëäÂàóË°®Ôºö</text>
        <text class="info-value">{{ baseUrl }}/api/public/notice/list</text>
      </view>
      <view class="info-item">
        <text class="info-label">Á≥ªÁªüÈÖçÁΩÆÔºö</text>
