      this.addLog('=== ÂºÄÂßãÊµãËØïËΩÆÊí≠ÂõæAPI ===')
      this.addLog(`ËØ∑Ê±ÇÂú∞ÂùÄ: ${this.baseUrl}/public/notice/banner`)

      try {
        const res = await getBannerList()

        this.addLog('‚úÖ ËØ∑Ê±ÇÊàêÂäü')
        this.addLog(`ÂìçÂ∫îÁ†Å: ${res.code}`)
        this.addLog(`ÂìçÂ∫îÊ∂àÊÅØ: ${res.message || 'Êó†'}`)

        if (res.data) {
          const data = Array.isArray(res.data) ? res.data : []
          this.addLog(`Êï∞ÊçÆÁ±ªÂûã: ${Array.isArray(res.data) ? 'Êï∞ÁªÑ' : typeof res.data}`)
          this.addLog(`Êï∞ÊçÆÈïøÂ∫¶: ${data.length}`)

          if (data.length > 0) {
            this.addLog('\nüìä ËΩÆÊí≠ÂõæÊï∞ÊçÆËØ¶ÊÉÖ:')
            data.forEach((item, index) => {
              this.addLog(`\n[${index + 1}] ID: ${item.id}`)
              this.addLog(`    Ê†áÈ¢ò: ${item.title}`)
              this.addLog(`    ÂõæÁâá: ${item.imgUrl || item.imageUrl || 'Êó†'}`)
              this.addLog(`    ÂÜÖÂÆπ: ${item.content || 'Êó†'}`)
              this.addLog(`    Áä∂ÊÄÅ: ${item.status}`)
              this.addLog(`    ÊéíÂ∫è: ${item.sort || 0}`)
            })
          } else {
            this.addLog('‚ö†Ô∏è ËøîÂõûÁ©∫Êï∞ÁªÑ')
          }
        } else {
          this.addLog('‚ö†Ô∏è ÂìçÂ∫î‰∏≠Ê≤°ÊúâdataÂ≠óÊÆµ')
