        this.addLog('‚ùå ËØ∑Ê±ÇÂ§±Ë¥•')
        this.addLog(`ÈîôËØØ‰ø°ÊÅØ: ${error.message}`)
        this.addLog(`ÈîôËØØËØ¶ÊÉÖ: ${JSON.stringify(error)}`)
