              this.addLog(`    Á±ªÂûã: ${item.type === 1 ? 'ÂÖ¨Âëä' : item.type === 2 ? 'Ê¥ªÂä®' : 'Êú™Áü•'}`)
              this.addLog(`    Áä∂ÊÄÅ: ${item.status === 1 ? 'ÊòæÁ§∫' : 'ÈöêËóè'}`)
              this.addLog(`    ÂàõÂª∫Êó∂Èó¥: ${item.createTime}`)
              this.addLog(`    ÂÜÖÂÆπ: ${item.content ? item.content.substring(0, 50) + '...' : 'Êó†'}`)
