        this.addLog(`ÂìçÂ∫îÁ†Å: ${res.code}`)
        this.addLog(`ÂìçÂ∫îÊ∂àÊÅØ: ${res.message || 'Êó†'}`)

        if (res.data) {
          this.addLog(`Êï∞ÊçÆÁ±ªÂûã: ${typeof res.data}`)
          this.addLog(`ÊòØÂê¶‰∏∫ÂØπË±°: ${typeof res.data === 'object'}`)
          this.addLog(`ÊòØÂê¶‰∏∫Êï∞ÁªÑ: ${Array.isArray(res.data)}`)
          this.addLog('\n‚öôÔ∏è ÈÖçÁΩÆÊï∞ÊçÆËØ¶ÊÉÖ:')

          if (typeof res.data === 'object' && !Array.isArray(res.data)) {
